import * as React from "react";
import '../styles/terminal.css'

class Terminal extends React.Component<{}, { inputBefore: string, inputAfter: string }> {
  constructor(props: any) {
    super(props)
    this.state = {
      inputBefore: '',
      inputAfter: ''
    };
  }

  render() {
    return (
      <div className={"box"} tabIndex={0} onKeyDown={evt => this.updateInputValue(evt)}>
        <div className="terminal-text">
          {this.state.inputValue}<b className="terminal-cursor">â–ˆ</b>
        </div>
      </div>
    )
  }

  updateInputValue(evt: React.KeyboardEvent<HTMLDivElement>) {
    const val = evt.key;
    this.setState({inputValue: val})
  }

}


export default Terminal